
import { IState, IUserState } from "@/typings";
import { computed, defineComponent, PropType, reactive, toRefs } from "vue";
import { useStore } from "vuex";
import { ATS } from "@/store/actionTypes";
import { useRoute, useRouter } from "vue-router";
import $message from "../Message";

export default defineComponent({
  name: "Header",
  props: {
    user: {
      type: Object as PropType<IUserState>,
      required: true,
    },
  },
  setup(props) {
    const store = useStore<IState>();
    const router = useRouter();
    const route = useRoute();
    const state = reactive({
      search: computed({
        get: () => store.state.search,
        set: (newVal) => {
          store.commit(ATS.SET_SEARCH, newVal);
        },
      }),
    });
    const account = [
      {
        text: "个人中心",
        link: `/profile/${props.user.info?._id}`,
      },
      {
        text: "收藏夹",
        link: "/bookmarks",
      },
      { divider: true },
      {
        text: "dashboard",
        link: "/dashboard",
      },
      {
        text: "写文章",
        link: "/create",
      },
      { divider: true },
      {
        text: "设置",
        link: "/settings",
      },
    ];

    const logout = () => {
      store.commit(ATS.LOGOUT);
      router.push("/login");
    };

    const gotoSearch = () => {
      if (state.search.trim() === "") {
        $message({
          type: "warn",
          message: "请输入搜索内容",
        });
        return;
      }

      router.push(
        `/search?q=${state.search}&filters=${route.query.filters || "User"}`
      );
    };

    return {
      ...toRefs(state),
      account,
      logout,
      gotoSearch,
    };
  },
});
